<template>
    <div style="text-align: center">
        <h1></h1>
        <el-tabs v-model="activeName">
            <el-tab-pane label="" name="first">
                <el-form
                        class="query-form"
                        ref="form"
                        :model="form"
                        label-width="100px">
                    <el-form-item label="">
                        <el-input v-model="form.bookid"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.type"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.press"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.author"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.price"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.count"></el-input>
                    </el-form-item>
                    <el-form-item
                            label=""
                            :rules="[
                      { type: 'number', message: ''}
                    ]">
                        <el-input v-model.number="form.year"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onNew"></el-button>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <el-input placeholder="" v-model="bookid">
                    <template slot="prepend"></template>
                </el-input>
                <el-input
                        placeholder=""
                        v-model="count">
                    <template slot="prepend"></template>
                    <el-button
                            @click="onChange"
                            slot="append"
                            icon="search"
                    ></el-button>
                </el-input>
            </el-tab-pane>
        </el-tabs>
        <el-alert
                v-if="status == 'success'"
                :title="statusInfo"
                type="success">
        </el-alert>
        <el-alert
                v-if="status == 'failed'"
                :title="statusInfo"
                type="error">
        </el-alert>
    </div>
</template>
<script>
    import {Network} from "../../../network";
    export default {
        data(){
            return {
                activeName : 'first',
                bookid: null,
                count: 0,
                form :{
                    bookid: '',
                    name : '',
                    type : '',
                    year: '',
                    price: '',
                    author : '',
                    press : '',
                    count: ''
                },
                result: [],
                status: null,
                statusInfo: ''
            }
        },
        methods:{
            onNew(){
                const that = this;
                const queryString = Object
                        .keys(this.form)
                        .map(key=>`${key}=${this.form[key]}`)
                        .join('&');
                Network.put(`/admin/book?${queryString}`,{},(ret)=>{
                    that.status = 'success';
                    that.statusInfo = 'success';
                },(message)=>{
                    that.status = 'failed';
                    that.statusInfo = message;
                })
            },
            onChange(){
                const that = this;
                Network.post(`/admin/book?bookid=${this.bookid}&count=${this.count}`,{},(ret)=>{
                    that.status = 'success';
                    that.statusInfo = 'success';
                },(message)=>{
                    that.status = 'failed';
                    that.statusInfo = message;
                })
            }
        }
    }
</script>
<style>

</style>


